.container {
  padding-left: 16px;
  padding-right: 16px;
  min-width: 289px;
  margin: 0 auto;
}
@media (min-width: 1920px) {
  .container {
    max-width: 1110px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* viewports */
.page-body {
  font-family: "Inter", "Arial", sans-serif;
}
.page-header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: nowrap;
  margin-top: 20px;
}
@media (min-width: 1920px) {
  .page-header__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
.page-header__search {
  width: 22px;
  height: 22px;
}
@media (min-width: 1920px) {
  .page-header__search {
    display: flex;
    flex-direction: column;
  }
  .page-header__search::after {
    position: absolute;
    content: url("../img/searchdesk.svg");
  }
}
.page-header__content {
  margin-right: 16px;
  position: relative;
}
@media (min-width: 1920px) {
  .page-header__content {
    display: flex;
    justify-content: flex-start;
  }
}
.page-header__like {
  border: none;
  background-color: #FFFFFF;
}
@media (min-width: 1920px) {
  .page-header__logo {
    margin-right: 108px;
    margin-top: 18px;
  }
}
.search-header {
  position: relative;
  width: 80px;
  margin: 0 auto;
  height: 42px;
}
.search-header__enter {
  height: 42px;
  width: 0;
  padding: 0 42px 0 15px;
  border: none;
  border-bottom: 2px solid transparent;
  outline: none;
  background: transparent;
  transition: 0.4s cubic-bezier(0, 0.8, 0, 1);
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}
.search-header__enter:focus {
  width: 300px;
  z-index: 1;
  border-bottom: 2px solid #9F9F9F;
}
@media (min-width: 1920px) {
  .search-header__enter {
    position: static;
    z-index: 0;
    background-image: none;
    width: 250px;
    border-bottom: 1px solid #9F9F9F;
    outline: none;
    padding-top: 9px;
    margin-left: 40px;
    cursor: pointer;
    display: block;
  }
}
.search-header__click {
  background-image: url("../img/searchphone.svg");
  background-repeat: no-repeat;
  background-color: #FFFFFF;
  border: none;
  height: 42px;
  width: 42px;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}
.search-header__click:focus {
  content: "";
  font-size: 16px;
  color: #DEDEDE;
}
@media (min-width: 1920px) {
  .search-header__click {
    display: none;
  }
}
.search-header::placeholder {
  display: flex;
  color: #9F9F9F;
  font-size: 18px;
  line-height: 21px;
}
input.search-header__enter:hover::placeholder {
  color: #000000;
}
.search-header__enter input:hover:-webkit-input-placeholder {
  color: #000000;
}
.search-header__enter input:hover:-moz-placeholder {
  color: #000000;
}
.search-header__enter input:hover:-ms-input-placeholder {
  color: #000000;
}
.main-nav__toggle {
  position: absolute;
  top: 10px;
  left: 0;
  z-index: 2;
  display: block;
  cursor: pointer;
  border: none;
  opacity: 0;
  width: 30px;
  height: 30px;
}
.main-nav::after {
  content: "";
  position: absolute;
  top: 21px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  margin-right: 20px;
}
@media (min-width: 1920px) {
  .main-nav {
    display: none;
  }
}
.page-header__opened .main-nav__list {
  display: none;
}
.main-nav--closed .main-nav__list {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  background-color: #FFFFFF;
  min-width: 100%;
  padding: 0;
  top: 48px;
  left: 0;
  position: absolute;
}
.main-nav__link {
  text-decoration: none;
  color: #000000;
}
.page-main {
  margin-top: 41px;
}
.page-main__enter {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-main__text {
  margin-right: 5px;
}
.page-main__link {
  text-decoration: none;
  color: #9F9F9F;
}
@media (min-width: 1920px) {
  .page-main__title {
    font-size: 40px;
    line-height: 46px;
  }
}
.site-list {
  text-decoration: none;
  list-style: none;
}
.breadcrumbs__link {
  text-decoration: none;
  font-size: 18px;
  line-height: 21px;
}
.breadcrumbs__link--current {
  color: #9F9F9F;
}
.breadcrumbs__list {
  display: none;
}
@media (min-width: 1920px) {
  .breadcrumbs__list {
    display: flex;
    gap: 41px;
    flex-wrap: nowrap;
    position: absolute;
    top: 132px;
    margin-left: 11px;
  }
}
@media (min-width: 1920px) {
  .breadcrumbs__item--fourth {
    padding-left: 251px;
  }
}
@media (min-width: 1920px) {
  .breadcrumbs__item {
    text-decoration: none;
    color: #9F9F9F;
    text-transform: uppercase;
    font-weight: 700;
  }
}
.order-form__wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
}
@media (min-width: 1920px) {
  .order-form__wrapper {
    grid-column: 1/-2;
    margin-right: 50px;
    margin-top: 20px;
  }
}
.order-form__information {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #9F9F9F;
  resize: none;
  outline: none;
}
@media (min-width: 1920px) {
  .order-form__information {
    cursor: pointer;
  }
}
.order-form__information:focus:invalid,
.order-form__information:not(:placeholder-shown):not(:focus):invalid {
  color: #D80909;
  border-color: #D80909;
}
.order-form__map {
  margin-bottom: 24px;
}
@media (min-width: 1920px) {
  .order-form__map {
    grid-column: 2/2;
    grid-row: 10/-1;
    margin-bottom: 0;
    position: relative;
  }
}
.order-form__sign {
  font-size: 12px;
  line-height: 14px;
  color: #9F9F9F;
}
.order-form__signature {
  font-size: 16px;
  color: #9F9F9F;
}
.order-form__packing {
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 1px solid #9F9F9F;
  font-size: 16px;
  line-height: 120%;
  color: #9F9F9F;
  appearance: none;
  background: url("../img/arrowmobile.svg") no-repeat right center;
  outline: 0;
}
@media (min-width: 1920px) {
  .order-form__packing {
    cursor: pointer;
  }
  .order-form__packing:hover {
    color: #000000;
  }
}
.order-form__heading {
  font-size: 26px;
  line-height: 30px;
}
.order-form__inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: url("../img/giftmobile.jpg") no-repeat;
  margin-top: 32px;
  border-bottom: 1px solid #DEDEDE;
  padding-bottom: 16px;
  position: relative;
}
@media (min-width: 1920px) {
  .order-form__inner {
    grid-column: 2/-3;
    margin-right: 49px;
    background: url("../img/giftdesk.jpg") no-repeat;
    grid-template-columns: 0.4fr 0.5fr;
  }
}
.order-form__link {
  grid-column: 3/-1;
  text-decoration: none;
  border: none;
  background-color: #FFFFFF;
  color: #9F9F9F;
}
.order-form__price {
  grid-column: 3/-1;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}
.order-form__name {
  grid-column: 2/-1;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}
.order-form__extra {
  grid-column: 2/-1;
  width: 81px;
  height: 34px;
  border: 1px solid #000000;
  box-sizing: border-box;
  padding-top: 6px;
  padding-left: 35px;
  position: relative;
}
.order-form__extra::after {
  position: absolute;
  content: url("../img/minusphone.svg");
  top: 5px;
  left: 0;
  width: 12px;
}
.order-form__extra::before {
  position: absolute;
  content: url("../img/plusmobile.svg");
  width: 12px;
  top: 5px;
  right: 0;
}
.order-form__more {
  right: 0;
  position: absolute;
  border: none;
  background-color: #FFFFFF;
}
.order-form__less {
  border: none;
  background-color: #FFFFFF;
  position: absolute;
  left: 0;
}
.order-form__figure {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}
.order-form__over {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (min-width: 1920px) {
  .order-form__over {
    background: #000000;
    opacity: 0.8;
    color: #FFFFFF;
    position: absolute;
    right: 0;
    bottom: 264px;
    min-width: 555px;
    padding-bottom: 10px;
  }
}
.order-form__over--map {
  display: none;
}
@media (min-width: 1920px) {
  .order-form__over--map {
    display: flex;
  }
}
.order-form__altogether {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (min-width: 1920px) {
  .order-form__altogether {
    display: none;
  }
}
.order-form__cost {
  font-size: 26px;
  line-height: 30px;
  font-weight: 700;
}
@media (min-width: 1920px) {
  .order-form__cost {
    font-size: 40px;
    line-height: 46px;
  }
}
.order-form__total {
  font-size: 26px;
  line-height: 30px;
  font-weight: 700;
}
@media (min-width: 1920px) {
  .order-form__total {
    font-size: 40px;
    line-height: 46px;
  }
}
.order-form__post {
  text-decoration: none;
  background-color: #FFFFFF;
  width: 100%;
  border: 1px solid #000000;
  box-sizing: border-box;
  padding-top: 20px;
  padding-bottom: 15px;
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
}
.order-form__post:hover {
  background: #000000;
  color: #FFFFFF;
}
@media (min-width: 1920px) {
  .order-form__post {
    width: 100%;
    grid-column: 1/-2;
    cursor: pointer;
    padding-bottom: 25px;
    margin-top: 25px;
  }
}
@media (min-width: 1920px) {
  .order-form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
  }
}
.order-form__description {
  display: contents;
}
@media (min-width: 1920px) {
  .order-form__description {
    grid-column: -2/-1;
    width: 153px;
    overflow: auto;
    display: flex;
    flex-wrap: wrap;
  }
}
.order-form__text {
  display: none;
}
@media (min-width: 1920px) {
  .order-form__text {
    display: block;
    color: #9F9F9F;
    font-size: 16px;
    font-family: "Inter", "Arial", sans-serif;
    font-weight: 400;
    line-height: 120%;
  }
}
.basket__link::after {
  position: absolute;
  content: "";
  background: #000000;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  right: -7px;
  top: 0;
}
.basket__figure {
  position: absolute;
  top: -12px;
  z-index: 2;
  right: -2px;
  color: #FFFFFF;
  font-size: 12px;
  line-height: 14px;
}
@media (min-width: 1920px) {
  .basket {
    display: grid;
    justify-content: end;
    grid-column: 6/6;
  }
}
.map__parametr {
  height: 200px;
  width: 100%;
}
@media (min-width: 1920px) {
  .map__parametr {
    height: 700px;
  }
}
@media (min-width: 1920px) {
  .logo {
    grid-column: -3/3;
    grid-row: 3/2;
  }
}
.like {
  grid-column: 5/-1;
  cursor: pointer;
}
@media (min-width: 1920px) {
  .like__button {
    cursor: pointer;
  }
}
.login__icon {
  display: none;
}
@media (min-width: 1920px) {
  .login__icon {
    display: block;
    cursor: pointer;
  }
}
.login__button {
  display: none;
}
@media (min-width: 1920px) {
  .login__button {
    display: block;
    border: none;
    background-color: #FFFFFF;
  }
}
@media (min-width: 1920px) {
  .login {
    grid-column: 4/-1;
    padding-left: 50px;
    justify-content: flex-end;
  }
}
.link {
  display: none;
}
@media (min-width: 1920px) {
  .link {
    display: flex;
    gap: 41px;
    flex-wrap: nowrap;
    position: absolute;
    top: 132px;
    margin-left: 11px;
  }
}
@media (min-width: 1920px) {
  .link__item--fourth {
    padding-left: 251px;
  }
}
@media (min-width: 1920px) {
  .link__item {
    text-decoration: none;
    color: #9F9F9F;
    text-transform: uppercase;
    font-weight: 700;
  }
}
